gure cite{font-size:smaller}.carousel .carousel-viewport .page .content article .doi{background-color:silver}.carousel .carousel-viewport .page .content article .doi u{-webkit-text-decoration:underline dotted;text-decoration:underline dotted;transition:all 0.25s ease-in-out}.carousel .carousel-viewport .page .content article .doi u:hover{color:blue}.carousel .carousel-viewport #about img{max-width:80%;height:auto;aspect-ratio:auto 1 / 1;border-radius:50%;margin:3em 0em}.carousel .carousel-viewport #about .content #education li::marker{font-family:FontAwesome;content:'\f19c'}.carousel .carousel-viewport #about .content #education li{padding:0.2em 0.5em}

